.container-book {
  text-align: center;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 60px;
  display: flex;
}

.book-decoration-up {
  width: 100%;
  padding-bottom: 20px;
}

.book-decoration-down {
  width: 70%;
  padding-bottom: 10px;
}

.book-author {
  color: #000;
  padding-bottom: 20px;
  font-family: Lato;
  font-size: 17px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.book-about {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.book-logo {
  padding-bottom: 30px;
}

.more-info-book {
  color: #f05;
  padding-top: 20px;
  padding-bottom: 40px;
  font-family: Lato;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: underline;
}

.book-text {
  padding-bottom: 30px;
}

.book-img {
  width: 590px;
  height: 656px;
  justify-content: center;
  margin-left: 20px;
  display: flex;
}

.btn-book {
  width: 250px;
  color: #fff;
  background: #000;
  border-radius: 100px;
}

.btn-book :hover {
  color: #fff;
  background-color: #f06;
  border-radius: 30px;
  transition: all .5s;
}

.btn-book p {
  padding: 10px;
}

.footer {
  color: #fff;
  background-color: #000;
  flex-direction: row;
  justify-content: space-around;
  padding: 50px 0;
  font-size: 12px;
  display: flex;
}

.title {
  width: 100%;
  margin-bottom: 25px;
}

.dargaud-logo {
  margin-bottom: 20px;
}

.dargaud .teaser {
  margin-top: 20px;
}

.social {
  width: 100px;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.media {
  width: 25px;
  flex-direction: row;
  display: flex;
}

.about h2 {
  margin-bottom: 10px;
  font-size: 30px;
}

.about {
  width: 25%;
}

.copyright {
  color: #fff;
  background-color: #000;
  flex-direction: row;
  justify-content: end;
  padding-bottom: 12px;
  font-size: 12px;
  display: flex;
}

.copyright p {
  padding-left: 20px;
}

.copyright .rights, .copyright .signature {
  margin-bottom: 10px;
  margin-right: 10px;
}

.header-section {
  height: 900px;
  min-width: 1000px;
  z-index: -900;
  background-color: #d2d2d2;
  border: 3px solid red;
  position: relative;
}

.button-header {
  min-width: 1000px;
  background-color: #676767;
  border: 3px solid red;
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;
  bottom: 90px;
}

.header-parallax {
  height: 85vh;
  flex-direction: column;
  display: flex;
  position: relative;
}

.parallax-layer {
  position: absolute;
  bottom: 0;
}

.layer-1, .layer-3 {
  top: 0%;
}

.layer-2 {
  width: 800px;
  position: absolute;
  bottom: 53%;
  left: 26%;
}

.spacer {
  height: 10vh;
}

.btn-scroll {
  z-index: 7;
  flex-direction: column;
  align-items: center;
  animation: bounce 2s ease-in-out infinite;
  display: flex;
  position: relative;
  top: 10px;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(10px);
  }

  50% {
    transform: translateY(30px);
  }
}

.carton-element {
  height: 450px;
  background-color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 300px;
  padding-bottom: 300px;
  display: flex;
  position: relative;
}

.carton p {
  color: #fff;
  text-align: center;
  font-family: Lora;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
  line-height: 25px;
}

.carton-up {
  padding-bottom: 50px;
}

.carton-down {
  padding-top: 50px;
}

